import React, { Component } from 'react'
import './App.css'
export default class App extends Component {
  constructor(props) {
    super(props);
    this.mainRef = React.createRef();
  }
  state = {
    belts: [
      { id: 1, status: "inactive", selected: false, text: 'вимкнена' },
      { id: 2, status: "inactive", selected: false, text: 'вимкнена' },
      { id: 3, status: "inactive", selected: false, text: 'вимкнена' },
      { id: 4, status: "inactive", selected: false, text: 'вимкнена' },
    ]
  }

  componentDidMount() {
    this.mainRef.current.focus();
    localStorage.setItem('log', '');
  }

  handleClick = (id, status, selected, text) => {
    const newBelt = {
      id,
      status,
      selected: !selected,
      text
    }
    const { belts } = this.state;
    belts[id - 1] = newBelt;
    this.setState({ belts })
  }

  prepareForWork = () => {
    const { belts } = this.state;
    belts.forEach(belt => {
      if (belt.selected === true) {
        belt.status = 'stoped';
        belt.text = 'готова до роботи';
        let log = localStorage.getItem('log');
        let today = new Date();
        log += `стрічка номер ${belt.id} готова до роботи, ${today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + ',' + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()}; \n`
        localStorage.setItem('log', log)
      }
    })
    this.setState({ belts })
  }

  handleKeyPress = (e) => {
    if (e.key === 'p') {
      this.prepareForWork();
    }
    if (e.key === 's') {
      this.stop();
    }
    if (e.key === 'Enter') {
      this.start();
    }
    if (e.key === 'Escape') {
      this.off();
    }
    if (e.key === 'f') {
      this.fire();
    }
    if (e.key === 'o') {
      this.overweight();
    }
  }

  stop = () => {
    const { belts } = this.state;
    belts.forEach(belt => {
      if (belt.selected === true) {
        belt.status = 'stoped';
        belt.text = 'призупинена';
        let log = localStorage.getItem('log');
        let today = new Date();
        log += `стрічка номер ${belt.id} призупинена, ${today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + ',' + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()} ; \n`
        localStorage.setItem('log', log)
      }
    })
    this.setState({ belts })
  }


  start = () => {
    const { belts } = this.state;
    belts.forEach(belt => {
      if (belt.selected === true) {
        if (belt.text === 'увімкнена') {
          window.alert('Вже працює');
        } else if (belt.text !== 'готова до роботи') {
          window.alert('Вибрані стрічки ще не готові до роботи');
        } else {
          belt.status = 'active';
          belt.text = 'увімкнена';
          let log = localStorage.getItem('log');
          let today = new Date();
          log += `стрічка номер ${belt.id} увімкнена, ${today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + ',' + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()}; \n`
          localStorage.setItem('log', log)
        }
      }
    })
    this.setState({ belts })
  }

  off = () => {
    const { belts } = this.state;
    belts.forEach(belt => {
      if (belt.selected === true) {
        if (belt.status === 'inactive') {
          window.alert('Стрічка вже вимкнена')
        } else {
          belt.status = 'inactive';
          belt.text = 'вимкнена';
          let log = localStorage.getItem('log');
          let today = new Date();
          log += `стрічка номер ${belt.id} вимкнена, ${today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + ',' + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()}; \n`
          localStorage.setItem('log', log)
        }
      }
    })
    this.setState({ belts })
  }
  fire = () => {
    window.alert('fire!')
    const { belts } = this.state;
    belts.forEach(belt => {
      belt.status = 'inactive';
      belt.text = 'виникла пожежа, вимкнена';
      let log = localStorage.getItem('log');
      let today = new Date();
      log += `стрічка номер ${belt.id} аварійно завершила роботу, ${today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + ',' + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()}; \n`
      localStorage.setItem('log', log)

    })
    this.setState({ belts })
  }

  overweight = () => {
    window.alert(`перевантаження ${(Math.random() * 10).toFixed(1)}кг!`)
    const { belts } = this.state;
    belts.forEach(belt => {
      if (belt.selected === true) {
        belt.status = 'inactive';
        belt.text = 'перенавантаження, вимкнена';
        let log = localStorage.getItem('log');
        let today = new Date();
        log += `стрічка номер ${belt.id} аварійно завершила роботу, ${today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + ',' + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()}; \n`
        localStorage.setItem('log', log)
      }
    })
    this.setState({ belts })
  }
  download = () => {
    var res = localStorage.getItem('log');

    var data = new Blob([res], { type: 'text/txt' }),
      URL = window.URL.createObjectURL(data),
      tempLink = document.createElement('a');
    tempLink.href = URL;
    tempLink.setAttribute('download', 'log.txt');
    tempLink.click();
  }
  render() {
    const { belts } = this.state;
    return (
      <div ref={this.mainRef} tabIndex="0" onKeyDown={(e) => this.handleKeyPress(e)} className="app">
        <h1 className="app_title">Оберіть стрічки для взаємодії</h1>
        <div className="wrapper">
          <div className="belt_main">
            {
              belts && belts.map(belt => {
                return (
                  <div key={belt.id} className="belt_block">
                    {
                      belt.status === 'active' &&
                      <div
                        onClick={() => this.handleClick(belt.id, belt.status, belt.selected, belt.text)}
                        className={belt.selected === true ? 'belt_active selected' : 'belt_active'}>
                      </div>
                    }
                    {
                      belt.status === 'inactive' &&
                      <div
                        onClick={() => this.handleClick(belt.id, belt.status, belt.selected, belt.text)}
                        className={belt.selected === true ? 'belt_inactive selected' : 'belt_inactive'}>
                      </div>
                    }
                    {
                      belt.status === 'stoped' &&
                      <div
                        onClick={() => this.handleClick(belt.id, belt.status, belt.selected, belt.text)}
                        className={belt.selected === true ? 'belt_stoped selected' : 'belt_stoped'}>
                      </div>
                    }

                    <span className="belt_title">{`стрічка номер ${belt.id}: ${belt.text}`}</span>
                  </div>
                )

              })
            }
            <button className="button_log" onClick={this.download}>Скачати log файл</button>
          </div>
          <div className="buttons_container">
            <button className="buttons_button" onClick={this.prepareForWork}>Приготувати стрічки до роботи   (p)</button>
            <button className="buttons_button" onClick={this.stop}>Призупинити   (s)</button>
            <button className="buttons_button" onClick={this.start}>Увімкнути  (Enter)</button>
            <button className="buttons_button" onClick={this.off}>Вимкнути   (Esc)</button>
            <button className="buttons_button" onClick={this.fire}>Сгенерувати пожежу   (f)</button>
            <button className="buttons_button" onClick={this.overweight}>Сгенерувати перевантаження   (o)</button>
          </div>
        </div>
      </div>
    )
  }
}


